<template>
  <base-layout
    :is-header-showed="!$vuetify.breakpoint.mobile"
    :white-background="$vuetify.breakpoint.mobile"
  >
    <template #header>
      <div class="d-flex justify-space-between align-center full-width">
        <div class="d-flex">
          <v-btn
            text
            depressed
            rounded
            color="transparent"
            min-width="auto"
            height="auto"
            class="px-0"
            :to="{ name: $constants.ROUTE_NAMES.CONTACTS }"
          >
            <v-img
              class="mr-4"
              alt="Back"
              :max-width="18"
              :src="require('@images/icons/drivers/back.svg')"
            />
          </v-btn>

          <h1 class="font-weight-bold primary-dark--text">
            {{ $route.meta.pageTitle }}
          </h1>
        </div>
      </div>
    </template>
    <template #content>
      <div class="content-page-wrapper d-flex flex-column flex-md-row flex-shrink-1 px-lg-6 py-lg-8 px-0 my-md-0 mx-lg-n2 mx-md-auto">
        <v-card
          width="730"
          :height="$vuetify.breakpoint.mobile ? '100%' : 'fit-content'"
          class="user-card py-lg-5 py-md-12 pt-12 pb-7 mx-lg-2 mx-md-2 mx-5 px-lg-5 px-sm-12"
          :flat="$vuetify.breakpoint.mobile"
        >
          <v-form
            ref="form"
            :class="[
              $vuetify.breakpoint.mobile ? 'd-flex flex-column justify-space-between' : '',
              'full-height'
            ]"
            v-model="form.valid"
            lazy-validation
          >
            <div>
              <base-field-input
                label="Name "
                :is-required="true"
                v-model="form.values.name"
                :rules="form.rules.name"
              />

              <base-field-input
                label="Address "
                :is-required="true"
                v-model="form.values.address"
                :rules="form.rules.address"
              />

              <base-field-input
                label="City "
                :is-required="true"
                v-model="form.values.city"
                :rules="form.rules.city"
              />

              <base-field-label-wrapper
                text="State "
                :is-required="true"
                label-for="state"
              >
                <v-autocomplete
                  class="body-2"
                  v-model="form.values.state"
                  :rules="form.rules.state"
                  :items="states.data"
                  item-text="name"
                  item-value="id"
                  outlined
                  dense
                >
                  <template v-slot:message="{ message }">
                    <base-field-error :message="message" />
                  </template>
                </v-autocomplete>
              </base-field-label-wrapper>

              <base-field-input
                label="Zip "
                :is-required="true"
                v-model="form.values.zip"
                :rules="form.rules.zip"
              />

              <base-field-input
                label="Contact name"
                v-model="form.values.contact_name"
              />

              <base-field-input
                label="Phone "
                :is-required="true"
                mask="+1 (###) ###-####"
                v-model="form.values.phone"
                :rules="form.rules.phone"
              />

              <base-field-input
                label="Fax"
                v-model="form.values.fax"
              />

              <base-field-textarea
                label="Internal notes"
                v-model="form.values.internal_notes"
              />

              <base-field-label-wrapper
                :text="fieldLabel"
                label-for="emails"
                class="email-label"
              >
                <template v-for="(email, index) in form.values.emails">
                  <base-field-input
                    :key="index"
                    class="body-2 font-weight-bold relative mb-1"
                    v-model="form.values.emails[index].email"
                    dense
                    outlined
                    :rules="form.rules.email"
                    :items="['email@email.com', 'email-example@email.com']"
                  >
                    <template v-slot:append-outer>
                      <v-btn
                        icon
                        width="36"
                        height="36"
                        :ripple="false"
                        @click="deleteEmail(index)"
                      >
                        <v-icon color="color-red">
                          mdi-delete-outline
                        </v-icon>
                      </v-btn>
                    </template>
                  </base-field-input>
                </template>
                <v-btn
                  class="px-0 primary--text no-border no-bg"
                  text
                  height="auto"
                  depressed
                  :ripple="false"
                  @click="addEmail"
                >
                  <v-icon left>
                    mdi-plus
                  </v-icon>
                  Add email
                </v-btn>
              </base-field-label-wrapper>
            </div>

            <v-btn
              :width="$vuetify.breakpoint.mobile ? '100%' : 200"
              :height="$vuetify.breakpoint.mobile ? 50 : 35"
              depressed
              color="primary"
              class="mt-10"
              :class="{ 'font-weight-bold': $vuetify.breakpoint.mobile }"
              :disabled="!form.valid || form.submitting"
              :loading="form.submitting"
              @click="handleSubmit"
            >
              SAVE CHANGES

              <v-img
                class="ml-2"
                :max-width="15"
                :src="require('@images/icons/contacts/save.svg')"
              />
            </v-btn>
          </v-form>
        </v-card>
      </div>
    </template>
  </base-layout>
</template>

<script>
import { createNamespacedHelpers } from 'vuex';
import { actions as dictionatiesActions } from '@/store/dictionaries/constants';
import { actions as contactsActions } from '@modules/contacts/constants';

import BaseLayout from '@components/BaseLayout';
import BaseFieldError from '@components/BaseFieldError';
import BaseFieldInput from '@components/BaseFieldInput';
import BaseFieldTextarea from '@components/BaseFieldTextarea';
import BaseFieldLabelWrapper from '@components/BaseFieldLabelWrapper/BaseFieldLabelWrapper.vue';

import {
  required,
  email,
  zip,
} from '@utils/helpers/validationRules';

const {
  mapActions: mapDictionatiesActions,
  mapState: mapDictionatiesState,
} = createNamespacedHelpers('dictionaries');

const {
  mapActions: mapContactsActions,
  mapState: getContactsState,
} = createNamespacedHelpers('contacts');

export default {
  name: 'ContactEdit',
  props: {
    id: {
      type: [Number, String],
      required: true,
    },
  },
  components: {
    BaseLayout,
    BaseFieldInput,
    BaseFieldError,
    BaseFieldTextarea,
    BaseFieldLabelWrapper,
  },
  data: () => ({
    form: {
      valid: false,
      submitting: false,
      values: {
        name: '',
        address: '',
        city: '',
        state: null,
        zip: '',
        contact_name: '',
        phone: '',
        fax: '',
        internal_notes: '',
        emails: [{
          email: '',
        }],
      },
      rules: {
        name: [required('Name')],
        address: [required('Address')],
        city: [required('City')],
        state: [required('State')],
        zip: [required('Zip'), zip],
        phone: [required('Phone')],
        email: [email],
      },
    },
    serverError: '',
    serverErrors: {},
  }),
  computed: {
    fieldLabel() {
      return this.form.values.emails.length > 1 ? 'Emails' : 'Email';
    },
    ...mapDictionatiesState(['states']),
    ...getContactsState(['contacts']),
  },
  methods: {
    addEmail() {
      this.form.values.emails.push({
        email: '',
      });
    },
    deleteEmail(index) {
      this.form.values.emails = this.form.values.emails.filter(
        (_, itemIndex) => itemIndex !== index,
      );
    },
    handleSubmit() {
      const isFormValid = this.$refs.form.validate();
      if (!isFormValid) return;
      this.form.submitting = true;
      this.editContact({
        id: this.id,
        data: {
          ...this.form.values,
          emails: this.form.values.emails.filter(({ email: val }) => !!val),
        },
      })
        .then(() => {
          this.$notificationSuccess('Contact edited');
          if (window.innerWidth >= 1264) {
            this.$router.push({ name: this.$constants.ROUTE_NAMES.CONTACT, params: { id: this.id } }).catch(() => {});
          } else {
            this.$router.push({ name: this.$constants.ROUTE_NAMES.MOBILE_CONTACT, params: { id: this.id } }).catch(() => {});
          }
        })
        .catch(() => {
          this.$notificationError('Contact wasn\'t edited');
        })
        .finally(() => {
          this.form.submitting = false;
        });
    },
    ...mapDictionatiesActions({
      getStates: dictionatiesActions.GET_STATES,
    }),
    ...mapContactsActions({
      editContact: contactsActions.EDIT_CONTACT,
      getContacts: contactsActions.GET_CONTACTS,
    }),
  },
  async mounted() {
    this.getStates();
    if (this.contacts.data.length) {
      const contact = this.contacts.data.find(({ id: contactId }) => Number(this.id) === Number(contactId));
      this.form.values = {
        name: contact.name,
        address: contact.address,
        city: contact.city,
        state: contact.state,
        zip: contact.zip,
        contact_name: contact.contact_name,
        phone: contact.phone,
        fax: contact.fax,
        internal_notes: contact.internal_notes,
        emails: contact.emails.length ? [...contact.emails] : [...this.form.values.emails],
      };
    }
  },
};
</script>

<style lang="scss" scoped>
.email-label ::v-deep label {
  display: flex !important;
}
.no-bg::before {
  background-color: transparent !important;
}
::v-deep .v-input__append-outer {
  margin-top: 0 !important;
  position: absolute;
  transform: translateX(calc(100% + 5px));
  right: 0;

  @media #{map-get($display-breakpoints, 'sm-and-down')} {
    margin-top: 5px !important;
  }
}
</style>
